.product-gallery{display:flex;padding:10px 100px}.product-gallery__container{display:flex;flex-direction:column;width:100%;gap:10px}.product-gallery__sort-section{display:flex;height:30px;align-items:center;justify-content:flex-end;width:100%;gap:15px;padding:15px;align-self:flex-end}.product-gallery__sort-section__sort{font-weight:500;font-size:16px;line-height:19px;color:#4f4f4f}.product-gallery__sort-section__view{font-weight:400;font-size:16px;line-height:19px;color:#000}.product-gallery__sort-section__dropdown{border:1px solid #e7e6ef;padding:8px 15px;text-align:center;outline:none;margin-right:10px}.product-gallery__filter-section{display:flex;flex-direction:column;gap:40px;padding-top:92px;width:400px}.product-gallery__filter-section__filter{display:flex;flex-direction:column;gap:17px}.product-gallery__filter-section__filter__title{font-weight:600;font-size:20px;line-height:30px;color:#282627;text-align:center;border-bottom:1px solid #000;padding-bottom:3px}.product-gallery__filter-section__colors{display:flex;gap:10px;flex-wrap:wrap}.input-label{display:flex;gap:10px;align-items:center;font-weight:600;font-size:16px;line-height:30px;color:#7f7f7f}.input-label__colorbox-blue{border:1px solid #5e37ff !important;background-color:#5e37ff !important}.input-label__colorbox-orange{border:1px solid #ff9437 !important;background-color:#ff9437 !important}.input-label__colorbox-brown{border:1px solid #57453b !important;background-color:#57453b !important}.input-label__colorbox-green{border:1px solid #157911 !important;background-color:#157911 !important}.input-label__colorbox-cream{border:1px solid #f5eee1 !important;background-color:#f5eee1 !important}.input-label input[type=checkbox]{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:20px;height:20px;border:1px solid #e91b1a;border-radius:2px;background-color:rgba(0,0,0,0);position:relative}.input-label input[type=checkbox]:checked{background-color:red}.input-label input[type=checkbox]::after{content:"✓";position:absolute;top:42%;left:50%;transform:translate(-50%, -50%);font-size:16px;color:#fff;opacity:0;font-weight:900}.input-label input[type=checkbox]:checked::after{opacity:1}.input-label__search-container{border:1px solid #bcbddb;background:#fff;border-radius:3px;width:203px;height:31px;display:flex;justify-content:space-around}.input-label__search-container>input{border:none;outline:none}.input-label__numOfReviews{font-weight:600;font-size:14px;line-height:16px;color:#282627}